  }

  .menyhere {
    display: inline;
    float: right;
    text-decoration: underline;
    margin-right: 15px;
    font-weight: 100;
    margin-top: 0.5em;
  }

  .menyhere a {
    background-color: rgba(255, 255, 255, 0.05);
    padding: 0.2rem 1rem;
    border-radius: 60px;
    color: white;
  }

  .meny a {
    color: white;
    text-decoration: none;
  }

  .meny a:hover {
