    border-radius: 60px;
  }
}
footer {
  background-image: linear-gradient(to right, #181818, #282828, #181818);
  color: white;
  width: 100%;
}
footer .footergrids {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 2em;
}
footer .footerp {
  max-width: 680px;
  padding-top: 1em;
  padding-bottom: 1em;
  margin-bottom: -1em;
  margin: auto;
  text-align: center;
}
footer .bottomfooter {
  background-color: #0b121a;
  background-color: black;
  max-width: 300px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin: auto;
}
footer .bottomfooter1 {
  background-color: #0b121a;
  background-color: black;
  bottom: 0;
  padding: 1em;
  margin-bottom: -2em;
}
footer .footerlogos {
  max-width: 40px;
}
footer .whitelink {
  color: white;
}
footer .whiteh3 {
  color: white;
  font-size: 1.4em;
  text-align: center;
}
@media only screen and (max-width: 900px) {
  footer {
    /*footer*/
  }
  footer .footerp {
    max-width: 680px;
    padding: 1em;
