html {
  background-color: var(--main-bg-color);
  scroll-behavior: smooth;
}

:root {
  --main-bg-color: #e8f8f5;
}

body {
  max-width: 1500px;
  margin: auto;
  font: 100% "IBM Plex Sans", sans-serif;
}

.homeh1 {
  text-align: center;
  color: white;
  font-weight: 400;
  font-size: 4em;
}

.h1booking {
  text-align: center;
  font-weight: 400;
  font-size: 2.8em;
}

.intro {
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  margin-right: 10px;
  padding-top: 12em;
}

.introsec {
  padding-top: 1em;
  padding-bottom: 3.5em;
  margin: auto;
  margin-bottom: 5em;
}

.introsec1 {
  padding-top: 1em;
  margin: auto;
  margin-top: -5em;
  margin-bottom: 5em;
  background: #371836;
  box-shadow: 0 2px 2px rgba(55, 24, 54, 0.2);
  color: white;
}

.centerdiv {
  max-width: 380px;
  margin: auto;
}

.introp {
  max-width: 540px;
  padding-left: 1em;
  padding-right: 1em;
  margin: auto;
}

.buttondiv {
  margin: auto;
  max-width: 200px;
  margin-top: 5em;
}

.button {
  font-size: 1.5em;
  border-radius: 30px;
  padding: 0.3em;
  padding-left: 1em;
  padding-right: 1em;
  text-align: center;
  background-color: #00254d;
  background-color: #002a57;
  color: white;
  font-weight: 100;
}

.button:hover {
  font-size: 1.5em;
  border-radius: 30px;
  padding: 0.3em;
  padding-left: 1em;
  padding-right: 1em;
  text-align: center;
  background-color: #001b38;
  color: white;
  font-weight: 100;
  cursor: pointer;
}

.h21 {
  text-align: center;
  font-size: 2em;
}

p {
  font-weight: 300;
  max-width: 32ch;
  line-height: 1.5em;
}

.pcenter {
  margin: auto;
  max-width: 400px;
  margin-bottom: 2em;
  color: white;
}

form {
  margin: auto;
  max-width: 400px;
}
form label {
  color: white;
}
form input[type=text]:focus {
  background-color: white;
  border: none;
}
form input[type=text], form input[type=email], form select, form textarea {
  width: 400px;
  padding: 12px;
  border: none;
  box-sizing: border-box;
  resize: vertical;
  border-bottom: solid 1px;
  margin: auto;
}
form input[type=submit] {
  background-color: white;
  color: black;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  width: 400px;
  cursor: pointer;
}
form input[type=submit]:hover {
  background-color: #03c0c0;
}

.container {
  background: #371836;
  box-shadow: 0 2px 2px rgba(55, 24, 54, 0.2);
  color: white;
  background-color: #251025;
  padding: 20px;
  margin-top: -5em;
}
